export function Home() {
    const [loading, setLoading] = useState(true)
    const [info, setInfo] = useState({
        day: NaN,
        score: NaN,
        startDate: moment().format('YYYY-MM-DD'),
        banFlag: false,
        banReason: "",
        expGroup: "",

        feedbackDay6: "",
        feedbackDay6Viewed: false,

        feedbackDay8: "",
        feedbackDay8Viewed: false,

        feedbackDay10: "",
        feedbackDay10Viewed: false,

        feedbackDay12: "",
        feedbackDay12Viewed: false,

        feedbackDay14: "",
        feedbackDay14Viewed: false,
    })
    const [refreshPop, setRefreshPop] = useState(false)
    const refreshRef = useRef(null)

    // const signOut = () => {
    //     // document.cookie = "username=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;";
    //     document.cookie = 'token=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;'
    //     window.location.href = '/'
    // }

    const elicitUserState = async () => {
        const res = await fetch(URL + 'api/info/', {
            method: 'POST',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
                Authorization: 'Bearer ' + getTokenFromCookie(),
            },
        })
        const response = await res.json()
        if (response.status === 'Success') {
            const msg = JSON.parse(response.message)
            setInfo(msg)
            setLoading(false)
        }
    }



    useEffect(() => {
        const response = async () => {
            const result = await validateLogin()
            if (result === false) {
                signOut()
                window.location.href = '/'
            }
        }
        response().catch(e => {
            console.error(e)
        })
    })

    useEffect(() => {
        const response = async () => {
            await elicitUserState()
        }

        response().catch(e => {
            console.error(e)
        })
    }, [])

    const acsMngr = new AcsMngr(info)
    const globalAccess = acsMngr.checkAccess(0, Rule.global)

    const Main = () => {
        return <div className={style.container}>
            <div className={style.rightPanel}>
                <div className={style.rightContainer}>
                    <div className={style.topTitle}> {getEncryptedUsernameFromCookie()}</div>
                    {globalAccess === true ? <></> :

                        <div className={style.errorContainer}>
                            <p></p>
                            {globalAccess.map((reason, index) => {
                                return <div className={style.errorCard} key={index}>{reason}</div>
                            })}
                            <p>9.924</p>
                        </div>


                    }
                    <div className={style.topContainer}>
                        <div className={style.topCard}>
                            <div>{
                                moment().diff(moment(info.startDate), 'days') + 1
                            }</div>
                        </div>
                        <div className={style.topCard}>
                            {(info.expGroup === 'Exp1' || info.expGroup === 'Exp2') ? <>
                                <p>9.9105

                                </p><p>
                                    152945105E105</p>
                            </> :
                                info.expGroup === 'Waitlist' ? <>
                                    <p>9.9105
                                    </p>
                                    <p>12945105103060100E1105</p>
                                </> : <><p></p></>}
                        </div>
                    </div>
                    <div className={style.bottomTitle}>
                        {' '}
                        <span
                            className={style.refresh}
                            onClick={() => {
                                if (!refreshPop) {
                                    elicitUserState()
                                    setRefreshPop(true)
                                    setTimeout(() => {
                                        setRefreshPop(false)
                                    }, 1000)
                                }
                            }}
                            ref={refreshRef}
                        >
                            
                        </span>
                        <Overlay target={refreshRef} show={refreshPop} placement="right">
                            <div
                                style={{
                                    backgroundColor: '#5A57FF',
                                    padding: '2px 5px',
                                    color: 'white',
                                    borderRadius: '2px',
                                    fontSize: '0.8em',
                                }}
                            >
                                
                            </div>
                        </Overlay>
                    </div>
                    <span style={{ fontSize: '0.8em', marginTop: '4px' }}> 4:00 am </span>
                    <div className={style.bottomContainer}>
                        {
                            (info.expGroup === 'Exp1' || info.expGroup === 'Exp2')
                                ?
                                tasks_expgroup.map(task => <Task
                                key={task.day}
                                currentDay={info.day}
                                days={task.day}
                                questionType={task.questionType}
                                timeToFinish={task.timeToFinish}
                                taskURL={task.taskURL}
                                    isOpen={acsMngr.checkAccess(task.day, task.rule)} />
                            )
                                :
                                info.expGroup === 'Waitlist' ? <>
                                    <p style={{ fontSize: '0.8em ' }}>*</p>
                                    {tasks_controlgroup.map(task => <Task
                                        key={task.day}
                                        currentDay={info.day}
                                        days={task.day}
                                        questionType={task.questionType}
                                        timeToFinish={task.timeToFinish}
                                        taskURL={task.taskURL}
                                        isOpen={acsMngr.checkAccess(task.day, task.rule)} />
                                    )}
                                </>
                                    :
                                    <p>{info.expGroup}</p>
                        }
                    </div>
                </div>
            </div>
        </div >
    }

    return (
        <div>
            <Header />
            {
                loading ?
                    <div className={style.loading}>
                        <Spinner id={style.spinner} animation="border" role="status" />
                    </div>
                    :
                    <Main />
            }
            <Footer />
        </div>
    )
}


